<template>
  <v-container>
    <!-- selection session -->
    <h1 class="ma-4 text-center">Verb Table</h1>
    <section class="selection" v-if="section =='selection'">
      <h1 class="mb-4">Please select the table </h1>
      <v-btn
        class="mr-4 mb-4"
        :class="{primary: includes(selected, key)}"
        :key="`table-${key}`" v-for="(table, key) in tables"
        @click="onClickSelect(key)"
      >
        {{ key }} ({{ table.length }})
      </v-btn>
      <div class="ma-4">
        ({{ questions.length }} verbs selected)
      </div>
      <div class="mt-4">
        <v-slider
          v-model="numQuestions"
          class="pt-4"
          :max="questions.length"
          min="1"
          thumb-label="always"
          label="Number Of Questions"
          step="1"
          ticks="always"
          tick-size="4"
          :disabled="isEmpty(selected)"
        />
        <v-checkbox
          v-model="isAudio"
          label="Audio"
        />
        <v-btn class="primary" block :disabled="isEmpty(selected)" @click="onClickStart">
          start
        </v-btn>
      </div>
    </section>
    <section class="playing" v-if="section =='playing'">
      <audio
        v-if="isAudio"
        ref="audio"
        preload
      >
        <source ref="audioSource" src="">
      </audio>
      <table>
        <tr
          :key="`question-${idx}`"
          v-for="(list, idx) in answers"
        >
          <td class="index">
            {{ idx + 1 }}
          </td>
          <td class="audio" v-if="isAudio">
            <v-btn @click="onClickAudio(questions[idx][0])">
              <v-icon large>
                mdi-play
              </v-icon>
            </v-btn>
          </td>
          <td :class="{mask: masks[idx] === 0, incorrect: errors[idx][0]}">
            <v-text-field
              placeholder="Present"
              class="pl-4 pr-4"
              v-model="list[0]"
              :readonly="masks[idx] === 0 || isFinished"
              :error-messages="errors[idx][0]"
              @input="onInput(idx, 0)"
            />
          </td>
          <td :class="{mask: masks[idx] === 1, incorrect: errors[idx][1]}">
            <v-text-field
              placeholder="Present (He/She/It)"
              class="pl-4 pr-4"
              v-model="list[1]"
              :readonly="masks[idx] === 1 || isFinished"
              :error-messages="errors[idx][1]"
              @input="onInput(idx, 1)"
            />
          </td>
          <td :class="{mask: masks[idx] === 2, incorrect: errors[idx][2]}">
            <v-text-field
              placeholder="Past"
              class="pl-4 pr-4"
              v-model="list[2]"
              :readonly="masks[idx] === 2 || isFinished"
              :error-messages="errors[idx][2]"
              @input="onInput(idx, 2)"
            />
          </td>
          <td :class="{mask: masks[idx] === 3, incorrect: errors[idx][3]}">
            <v-text-field
              placeholder="Present Continuous"
              class="pl-4 pr-4"
              v-model="list[3]"
              :readonly="masks[idx] === 3 || isFinished"
              :error-messages="errors[idx][3]"
              @input="onInput(idx, 3)"
            />
          </td>
        </tr>
      </table>

      <v-btn class="mt-4 mb-4 primary" block @click="onClickSubmit" v-if="!isFinished">
        Submit
        <span v-if="numTotal">{{ numCorrect }}/{{ numTotal }}</span>
      </v-btn>
      <h3 clsss="pa-4" v-if="isFinished">
        CONGRATULATION, you make all correct! ({{ numCorrect }}/{{ numTotal }})
      </h3>
      <v-btn class="mt-4 mb-4" @click="onClickShowAnswer" v-if="!isFinished">
        Check And Show Answer
        <span v-if="numTotal">{{ numCorrect }}/{{ numTotal }}</span>
      </v-btn>
      <br>
      <!-- <v-btn class="mt-4 mb-4" @click="onClickReset">
        reset
      </v-btn> -->

      <v-dialog
        v-model="dialog"
        width="500"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            v-bind="attrs"
            v-on="on"
          >
            Reset
          </v-btn>
        </template>

        <v-card>
          <v-card-text class="pa-4">
            Are you sure you want to reset?
          </v-card-text>
          <v-divider />
          <v-card-actions>
            <v-spacer />
            <v-btn
              text
              @click="dialog = false"
            >
              Cancel
            </v-btn>
            <v-btn
              color="primary"
              text
              @click="onClickReset"
            >
              Yes
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </section>
  </v-container>
</template>

<script>
import _ from 'lodash'
import XlsxService from '../services/XlsxService'

export default {
  data () {
    return {
      isEmpty: _.isEmpty,
      includes: _.includes,
      tables: null,
      selected: [],
      questions: [],
      section: 'selection',
      masks: [],
      answers: [],
      errors: [],
      numTotal: 0,
      numCorrect: 0,
      numQuestions: 0,
      isFinished: false,
      dialog: null,
      isAudio: true
    }
  },
  async mounted () {
    this.tables = await XlsxService.get()

    // this.debugStart()
  },
  methods: {
    isIncorrect (idx) {
      const incorrect = !_.isEmpty(_.compact(this.errors[idx]))
      return incorrect
    },
    onInput (idx, subIdx) {
      _.set(this.errors, `${idx}.${subIdx}`, null)
      this.errors = _.clone(this.errors)
    },
    async debugStart () {
      this.selected = _.keys(this.tables)
      this.onClickStart()
    },
    async onClickSelect (key) {
      if (_.includes(this.selected, key)) {
        this.selected = _.difference(this.selected, [key])
      } else {
        this.selected = _.union(this.selected, [key])
      }
      const list = _.map(this.selected, (id) => this.tables[id])
      this.questions = _.flatten(list)
      this.numQuestions = _.min([5, this.questions.length])
    },
    onClickStart () {
      this.isFinished = false
      this.section = 'playing'
      const list = _.map(this.selected, (id) => this.tables[id])
      this.questions = _.flatten(list)
      this.numQuestions = this.numQuestions || this.questions.length
      this.questions = _.take(_.shuffle(this.questions), this.numQuestions)

      this.masks = _.map(this.questions, (items) => {
        if (this.isAudio) {
          return -1
        }
        const newItems = _.compact(items)
        const showItem = _.sample(newItems)
        return _.indexOf(items, showItem)
      })

      this.answers = []
      _.each(this.masks, (mark, idx) => {
        const question = this.questions[idx]
        _.set(this.answers, `${idx}.${mark}`, question[mark])
        _.set(this.errors, `${idx}`, [])
      })
    },
    checkAnswers (isShowAnswer) {
      this.isFinished = true
      this.numTotal = 0
      this.numCorrect = 0
      _.each(this.questions, (list, idx) => {
        const aList = this.answers[idx]
        _.each(list, (value, subIdx) => {
          if (_.isEmpty(value)) {
            return
          }
          if (subIdx === this.masks[idx]) {
            return
          }
          this.numTotal += 1
          if (_.trim(_.toLower(aList[subIdx])) !== _.trim(_.toLower(value))) {
            if (isShowAnswer) {
              _.set(this.errors, `${idx}.${subIdx}`, _.trim(_.toLower(value)))
            } else {
              _.set(this.errors, `${idx}.${subIdx}`, 'wrong')
            }
            this.isFinished = false
          } else {
            _.set(this.errors, `${idx}.${subIdx}`, null)
            this.numCorrect += 1
          }
        })
      })
      this.errors = _.clone(this.errors)
    },
    onClickSubmit () {
      this.checkAnswers()
    },
    onClickShowAnswer () {
      this.checkAnswers(true)
    },
    onClickReset () {
      this.dialog = false
      this.isFinished = false
      this.numTotal = 0
      this.numCorrect = 0
      this.section = 'selection'
      const list = _.map(this.selected, (id) => this.tables[id])
      this.questions = _.flatten(list)
    },
    onClickAudio (word) {
      const { audio, audioSource } = this.$refs
      audio.src = `./mp3/${word}.mp3`
      audioSource.src = `./mp3/${word}.mp3`
      audioSource.type = 'audio/mpeg'
      audio.play()
    }
  }
}
</script>

<style lang="scss" scoped >
table {
  width: 100%;
  // border-collapse: collapse;

  td {
    width: 25%;
    padding-right: 30px;

    &.index {
      width: 0px;
    }
    &.audio {
      width: 0px;
    }
    &.incorrect {
      background-color: #ffeeee;
    }
  }
}
.mask {
  pointer-events: none;
  background-color: #ddffdd;
}
</style>
