import autoBind from 'auto-bind'
import axios from 'axios/dist/axios.min'
// const { ipcRenderer } = window.require('electron')

class XlsxService {
  constructor () {
    autoBind(this)
  }

  onChangeConfig (callback) {
    this.onChangeConfigCallback = callback
  }

  async get () {
    const response = await axios.get('./verb-table.json')
    return response.data
    // return await ipcRenderer.invoke('XlsxManager.get')
  }
}

export default new XlsxService()
